export default {
	data() {
		return {
			userInfor:'',
		}
	},
	onLoad(option) {
		this.userInfor=JSON.parse(decodeURIComponent(option.data))
	},
	methods: {
		//去个人信息管理页
		toUpdateInfor(){
			uni.redirectTo({
				url:'../updateInfor/updateInfor?data='+encodeURIComponent(JSON.stringify(this.userInfor))
			})
		},
		//退出登录
		loginOut(){
			uni.switchTab({
			    url: '/pages/index/index'
			});
		}
	}
}
